import React, { useEffect, useRef, useState } from 'react'
import { FaceTracker } from '../FaceTracker'
import { ARSceneAFrame } from '../ARSceneAFrame'
import { useRA } from '../../contexts/RAContext'
import type { ScreenType, TransitionType, TransitionDirection } from '../../types/screens'
import '../../styles/ar-screen.css'

interface ARScreenProps {
  onNavigate: (screen: ScreenType, transition?: TransitionType, direction?: TransitionDirection) => void
  title?: string
  subtitle?: string
  backgroundImage?: string
}

export const ARScreen: React.FC<ARScreenProps> = () => {
  const { raData } = useRA()
  const config = raData?.configuracoes || {}
  const usarAFrame = config.usarAFrame !== false
  const usarVideo = config.usarVideo !== false
  const usarFaceTracking = config.usarFaceTracking !== false
  const espelharVideo = config.espelharVideo === true
  const mostrarDebug = config.mostrarDebug === true
  const markerType = config.markerType || 'pattern'
  const markerUrl = config.markerUrl || (markerType === 'nft'
    ? '/assets/images/image-marker/imagem'
    : 'https://cdn.jsdelivr.net/gh/AR-js-org/AR.js@master/data/data/patt.hiro')
  const markerImageUrl = config.markerImageUrl || 'https://jeromeetienne.github.io/AR.js/data/images/HIRO.jpg'

  const [arLoading, setArLoading] = useState(true)
  const [markerDetected, setMarkerDetected] = useState(false)
  const [debugInfo, setDebugInfo] = useState<any>({})
  const videoRef = useRef<HTMLVideoElement | null>(null)
  const mediaStreamRef = useRef<MediaStream | null>(null)
  const debugIntervalRef = useRef<NodeJS.Timeout | null>(null)

  // Mostrar vídeo - usar o vídeo global (se habilitado)
  useEffect(() => {
    if (!usarVideo) {
      setArLoading(false)
      return
    }

    async function setupCamera() {
      try {
        // Usar o vídeo global que já foi criado
        const video = document.getElementById('arjs-video') as HTMLVideoElement
        if (!video || !video.srcObject) {
          console.warn('Vídeo global não encontrado, aguardando...')
          await new Promise(resolve => setTimeout(resolve, 1000))
          const retryVideo = document.getElementById('arjs-video') as HTMLVideoElement
          if (!retryVideo || !retryVideo.srcObject) {
            console.error('Vídeo ainda não disponível')
            setArLoading(false)
            return
          }
        }

        const videoElement = video || (document.getElementById('arjs-video') as HTMLVideoElement)
        if (videoElement && videoElement.srcObject) {
          // Vídeo já existe, apenas mostrar
          videoElement.classList.add('ar-video-visible')

          // Aplicar espelhamento baseado na configuração
          if (espelharVideo) {
            videoElement.style.transform = 'scaleX(-1)'
          } else {
            videoElement.style.transform = 'none'
          }

          videoRef.current = videoElement
          mediaStreamRef.current = videoElement.srcObject as MediaStream

          setArLoading(false)
        } else {
          console.error('Vídeo não tem stream')
          setArLoading(false)
        }
      } catch (err) {
        console.error('Erro ao configurar câmera:', err)
        setArLoading(false)
      }
    }

    setupCamera()

    return () => {
      // Não limpar aqui - deixar o vídeo para o FaceTracker
    }
  }, [usarVideo, espelharVideo])

  // Adicionar elementos do A-Frame Scene e Image Tracking
  useEffect(() => {
    if (!usarAFrame) return

    const sceneEl = document.querySelector('a-scene#ar-scene-main') as any
    if (!sceneEl) {
      console.warn('A-Frame Scene não encontrado')
      return
    }

    // Configurar AR.js no scene se ainda não estiver configurado
    if (!sceneEl.hasAttribute('arjs')) {
      // Configuração do AR.js - suporta tanto pattern quanto NFT markers
      const arjsConfig = markerType === 'nft'
        ? 'trackingMethod: best; sourceType: webcam; debugUIEnabled: false;'
        : 'trackingMethod: best; sourceType: webcam; debugUIEnabled: false; detectionMode: mono_and_matrix; matrixCodeType: 3x3; cameraParametersUrl: https://cdn.jsdelivr.net/gh/AR-js-org/AR.js@master/data/data/camera_para.dat; maxDetectionRate: 60; canvasWidth: 640; canvasHeight: 480;'
      sceneEl.setAttribute('arjs', arjsConfig)

      // Para NFT markers, usar logarithmicDepthBuffer conforme documentação
      if (markerType === 'nft') {
        const currentRenderer = sceneEl.getAttribute('renderer')
        // Converter para string se for objeto
        const rendererStr = typeof currentRenderer === 'string'
          ? currentRenderer
          : typeof currentRenderer === 'object' && currentRenderer !== null
            ? Object.entries(currentRenderer).map(([k, v]) => `${k}: ${v}`).join('; ')
            : ''

        // Substituir ou adicionar logarithmicDepthBuffer: true
        const updatedRenderer = rendererStr.includes('logarithmicDepthBuffer')
          ? rendererStr.replace(/logarithmicDepthBuffer:\s*(true|false)/, 'logarithmicDepthBuffer: true')
          : `${rendererStr ? rendererStr + '; ' : ''}logarithmicDepthBuffer: true`
        sceneEl.setAttribute('renderer', updatedRenderer)
      }

      console.log(`✅ AR.js configurado no scene (${markerType.toUpperCase()} mode)`)
    }

    // Adicionar câmera (AR.js gerencia a câmera, mas precisamos de uma entidade camera para o A-Frame)
    let camera = document.getElementById('camera')
    if (!camera) {
      camera = document.createElement('a-entity')
      camera.id = 'camera'
      // Para AR.js, a câmera precisa estar configurada de forma específica
      camera.setAttribute('camera', 'active: true; fov: 60; near: 0.1; far: 10000')
      // Desabilitar look-controls quando usando AR.js (o AR.js controla a câmera)
      camera.setAttribute('look-controls', 'enabled: false')
      camera.setAttribute('wasd-controls', 'enabled: false')
      camera.setAttribute('position', '0 1.6 0')
      sceneEl.appendChild(camera)
    }

    // Adicionar luzes
    let ambientLight = sceneEl.querySelector('#ambient-light')
    if (!ambientLight) {
      ambientLight = document.createElement('a-entity')
      ambientLight.id = 'ambient-light'
      ambientLight.setAttribute('light', 'type: ambient; color: #888')
      sceneEl.appendChild(ambientLight)
    }

    let directionalLight = sceneEl.querySelector('#directional-light')
    if (!directionalLight) {
      directionalLight = document.createElement('a-entity')
      directionalLight.id = 'directional-light'
      directionalLight.setAttribute('light', 'type: directional; color: #fff; intensity: 0.6')
      directionalLight.setAttribute('position', '1 1 1')
      sceneEl.appendChild(directionalLight)
    }

    // Adicionar Image Tracking com AR.js
    // Para usar: mostre o marker na câmera (pode ser impresso ou em outra tela)
    let imageMarker = document.getElementById('image-marker')
    if (!imageMarker) {
      // Criar marker de imagem - usar a-nft para NFT, a-marker para pattern
      if (markerType === 'nft') {
        imageMarker = document.createElement('a-nft')
        imageMarker.id = 'image-marker'
        imageMarker.setAttribute('type', 'nft')
        imageMarker.setAttribute('url', markerUrl)
        // Smooth attributes para NFT markers (conforme documentação)
        imageMarker.setAttribute('smooth', 'true')
        imageMarker.setAttribute('smoothCount', '10')
        imageMarker.setAttribute('smoothTolerance', '.01')
        imageMarker.setAttribute('smoothThreshold', '5')
      } else {
        imageMarker = document.createElement('a-marker')
        imageMarker.id = 'image-marker'
        imageMarker.setAttribute('type', 'pattern')
        imageMarker.setAttribute('url', markerUrl)
        // Smooth attributes para pattern markers
        imageMarker.setAttribute('smooth', 'true')
        imageMarker.setAttribute('smoothCount', '10')
        imageMarker.setAttribute('smoothTolerance', '0.01')
        imageMarker.setAttribute('smoothThreshold', '5')
      }

      // Criar cubo que aparece quando o marker é detectado
      const trackedCube = document.createElement('a-box')
      trackedCube.setAttribute('position', '0 0.5 0')
      trackedCube.setAttribute('rotation', '0 45 45')
      trackedCube.setAttribute('color', '#4CC3D9')
      trackedCube.setAttribute('width', '1')
      trackedCube.setAttribute('height', '1')
      trackedCube.setAttribute('depth', '1')

      // Animação de rotação contínua
      trackedCube.setAttribute('animation', 'property: rotation; to: 0 405 45; loop: true; dur: 10000')

      // Adicionar animação de escala para feedback visual
      const scaleAnimation = document.createElement('a-animation')
      scaleAnimation.setAttribute('attribute', 'scale')
      scaleAnimation.setAttribute('dur', '2000')
      scaleAnimation.setAttribute('repeat', 'indefinite')
      scaleAnimation.setAttribute('from', '1 1 1')
      scaleAnimation.setAttribute('to', '1.2 1.2 1.2')
      scaleAnimation.setAttribute('direction', 'alternate')
      trackedCube.appendChild(scaleAnimation)

      imageMarker.appendChild(trackedCube)
      sceneEl.appendChild(imageMarker)

      // Adicionar listeners para debug
      if (mostrarDebug) {
        // Eventos do AR.js para markers
        const onMarkerFound = () => {
          setMarkerDetected(true)
          const markerEl = imageMarker as any
          const object3D = markerEl.object3D
          if (object3D) {
            setDebugInfo({
              position: {
                x: object3D.position?.x || 0,
                y: object3D.position?.y || 0,
                z: object3D.position?.z || 0
              },
              rotation: {
                x: object3D.rotation?.x || 0,
                y: object3D.rotation?.y || 0,
                z: object3D.rotation?.z || 0
              },
              scale: {
                x: object3D.scale?.x || 1,
                y: object3D.scale?.y || 1,
                z: object3D.scale?.z || 1
              },
              visible: true
            })
          }
        }

        const onMarkerLost = () => {
          setMarkerDetected(false)
          setDebugInfo((prev: any) => ({ ...prev, visible: false }))
        }

        // Tentar diferentes nomes de eventos
        imageMarker.addEventListener('markerFound', onMarkerFound)
        imageMarker.addEventListener('found', onMarkerFound)
        imageMarker.addEventListener('arjs-video-loaded', onMarkerFound)

        imageMarker.addEventListener('markerLost', onMarkerLost)
        imageMarker.addEventListener('lost', onMarkerLost)

        // Atualizar debug periodicamente quando detectado
        debugIntervalRef.current = setInterval(() => {
          const markerEl = imageMarker as any
          const object3D = markerEl?.object3D
          if (object3D && object3D.visible) {
            setMarkerDetected(true)
            setDebugInfo({
              position: {
                x: object3D.position?.x || 0,
                y: object3D.position?.y || 0,
                z: object3D.position?.z || 0
              },
              rotation: {
                x: object3D.rotation?.x || 0,
                y: object3D.rotation?.y || 0,
                z: object3D.rotation?.z || 0
              },
              visible: true
            })
          } else {
            setMarkerDetected(false)
            setDebugInfo((prev: any) => ({ ...prev, visible: false }))
          }
        }, 100)
      }

      console.log(`✅ Image tracking marker adicionado (${markerType.toUpperCase()}) - Mostre o marker na câmera`)
      console.log(`📄 Marker Image: ${markerImageUrl}`)
      console.log(`🔗 Marker URL: ${markerUrl}`)
      if (markerType === 'nft') {
        console.log('📦 NFT Marker: Certifique-se de que os arquivos .iset, .fset e .fset3 estão no mesmo diretório')
      }
    }

    // Limpar objetos quando o componente desmontar
    return () => {
      if (debugIntervalRef.current) {
        clearInterval(debugIntervalRef.current)
        debugIntervalRef.current = null
      }
      const cameraEl = document.getElementById('camera')
      if (cameraEl) {
        cameraEl.remove()
      }
      const ambientLightEl = sceneEl.querySelector('#ambient-light')
      if (ambientLightEl) {
        ambientLightEl.remove()
      }
      const directionalLightEl = sceneEl.querySelector('#directional-light')
      if (directionalLightEl) {
        directionalLightEl.remove()
      }
      const imageMarkerEl = document.getElementById('image-marker')
      if (imageMarkerEl) {
        imageMarkerEl.remove()
      }
    }
  }, [usarAFrame, markerType, markerUrl, markerImageUrl, mostrarDebug])

  return (
    <div className="ar-game-screen">
      {/* Loading overlay */}
      {arLoading && (
        <div className="ar-loading-overlay">
          <div className="ar-loading-content">
            <div className="ar-loading-spinner"></div>
            <p className="ar-loading-text">Preparando AR...</p>
          </div>
        </div>
      )}

      {/* Debug Panel */}
      {mostrarDebug && (
        <div style={{
          position: 'fixed',
          top: '10px',
          left: '10px',
          background: 'rgba(0, 0, 0, 0.8)',
          color: '#fff',
          padding: '15px',
          borderRadius: '8px',
          fontFamily: 'monospace',
          fontSize: '12px',
          zIndex: 10000,
          maxWidth: '300px',
          lineHeight: '1.6'
        }}>
          <div style={{ fontWeight: 'bold', marginBottom: '10px', fontSize: '14px', borderBottom: '1px solid #555', paddingBottom: '5px' }}>
            🔍 DEBUG AR
          </div>
          <div style={{ marginBottom: '5px' }}>
            <strong>Marker Type:</strong> {markerType.toUpperCase()}
          </div>
          <div style={{ marginBottom: '5px' }}>
            <strong>Marker Status:</strong>
            <span style={{ color: markerDetected ? '#0f0' : '#f00', marginLeft: '5px' }}>
              {markerDetected ? '✅ DETECTADO' : '❌ NÃO DETECTADO'}
            </span>
          </div>
          <div style={{ marginBottom: '5px' }}>
            <strong>Video:</strong> {usarVideo ? '✅' : '❌'}
          </div>
          <div style={{ marginBottom: '5px' }}>
            <strong>Video Espelhado:</strong> {espelharVideo ? '✅ SIM' : '❌ NÃO'}
          </div>
          <div style={{ marginBottom: '5px' }}>
            <strong>AFrame:</strong> {usarAFrame ? '✅' : '❌'}
          </div>
          <div style={{ marginBottom: '5px' }}>
            <strong>Face Tracking:</strong> {usarFaceTracking ? '✅' : '❌'}
          </div>
          {markerDetected && debugInfo.visible && (
            <div style={{ marginTop: '10px', paddingTop: '10px', borderTop: '1px solid #555' }}>
              <div style={{ marginBottom: '5px' }}>
                <strong>Position:</strong> X:{debugInfo.position?.x?.toFixed(2) || '0'} Y:{debugInfo.position?.y?.toFixed(2) || '0'} Z:{debugInfo.position?.z?.toFixed(2) || '0'}
              </div>
              <div style={{ marginBottom: '5px' }}>
                <strong>Rotation:</strong> X:{debugInfo.rotation?.x?.toFixed(2) || '0'} Y:{debugInfo.rotation?.y?.toFixed(2) || '0'} Z:{debugInfo.rotation?.z?.toFixed(2) || '0'}
              </div>
            </div>
          )}
          <div style={{ marginTop: '10px', paddingTop: '10px', borderTop: '1px solid #555', fontSize: '10px', color: '#aaa' }}>
            <div>Marker URL: {markerUrl}</div>
          </div>
        </div>
      )}

      {/* A-Frame Scene (se habilitado) */}
      {usarAFrame && (
        <ARSceneAFrame />
      )}

      {/* Face Tracker (se habilitado) */}
      {usarFaceTracking && (
        <FaceTracker enabled={true} />
      )}
    </div>
  )
}
